body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-size: 1.2rem;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.Input {
    width: 100%;
    box-sizing: border-box;
    padding-bottom: 10px;
    text-align: center;
    outline: none;
}

.Label {
    color: black;
    font-weight: bold;
    display: block;
    margin-bottom: 8px;
}

.InputElement {
    width: 100%;
    display: block;
    outline: none;
    font: inherit;
    box-sizing: border-box;
    padding: 6px 10px;
    background-color: white;
    margin: auto;
    border-radius: 5px;
}

.InputElement:focus {
    outline: none;
    background-color: #eeeeee;
}

.Invalid {
    border: 2px solid salmon;
}

.Button {

}

.Form {
    width: 100%;
}

.FormContainer {
    width: 80%;
    border: 1px solid #eee;
    border-radius: 10px;
    box-shadow: 0 2px 15px #aaaaaa;
    text-align: center;
    padding: 10px;
    box-sizing: border-box;
    margin: 15px auto;
}

.ResultContainer {
    text-align: left;
    min-height: 350px;
}

.FormButton {
    width: 100%;
    cursor: pointer;
    background-color: darkblue;
    box-sizing: border-box;
    padding: 10px;
    text-align: center;
    border-radius: 5px;
    color: white;
}

.FormButton:enabled {
    -webkit-transition: background-color 0.5s;
    transition: background-color 0.5s;
}

.FormButton:disabled {
    cursor: not-allowed;
    background-color: lightslategrey;
}

.Error {
    text-align: center;
    font-weight: bold;
}
@media (min-width: 700px) {
    .Form {
        width: 600px;
    }
}

.Loader,
.Loader:before,
.Loader:after {
    background: #004080;
    -webkit-animation: load1 1s infinite ease-in-out;
    animation: load1 1s infinite ease-in-out;
    width: 1em;
    height: 4em;
}
.Loader {
    color: #004080;
    text-indent: -9999em;
    margin: 88px auto;
    position: relative;
    font-size: 11px;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-animation-delay: -0.16s;
    animation-delay: -0.16s;
}
.Loader:before,
.Loader:after {
    position: absolute;
    top: 0;
    content: '';
}
.Loader:before {
    left: -1.5em;
    -webkit-animation-delay: -0.32s;
    animation-delay: -0.32s;
}
.Loader:after {
    left: 1.5em;
}
@-webkit-keyframes load1 {
    0%,
    80%,
    100% {
        box-shadow: 0 0;
        height: 4em;
    }
    40% {
        box-shadow: 0 -2em;
        height: 5em;
    }
}
@keyframes load1 {
    0%,
    80%,
    100% {
        box-shadow: 0 0;
        height: 4em;
    }
    40% {
        box-shadow: 0 -2em;
        height: 5em;
    }
}
